import React from 'react'
import { FormattedMessage } from 'react-intl'
import { useDispatch } from 'react-redux'
import { Card, CardBody, Row, Col, Button } from 'reactstrap'
import useDeviceDetect from '../../../customHooks/useDeviceDetect'
import { goBackHomePage } from '../../../redux/actions/navbar/Index'

const ContactTab = () => {
  const { isMobile } = useDeviceDetect()
  const dispatch = useDispatch()

  const onClickBackHome = () => {
    dispatch(goBackHomePage())
  }
  return (
    <React.Fragment>
      <Row className='d-flex justify-content-center'>
        <Col lg='4' md='6' sm='12'>
          <Card>
            <CardBody className='w-300px mx-auto'>
              <div className='box-content'>
                <a href='mailto:lienhe@inon.vn' target='_blank'>
                  lienhe@inon.vn
                </a>
              </div>
              <div className='card-btns d-flex justify-content-center mt-2'>
                <Button.Ripple className='gradient-light-primary text-white'>
                  <a
                    className='text-white'
                    href='mailto:lienhe@inon.vn'
                    target='_blank'
                  >
                    Gửi mail
                  </a>
                </Button.Ripple>
              </div>
            </CardBody>
          </Card>
        </Col>
        <Col lg='4' md='6' sm='12'>
          <Card>
            <CardBody className='w-300px mx-auto'>
              <div className='box-content'>
                <h5>0979 87 85 82</h5>
              </div>

              <div className='card-btns d-flex justify-content-center mt-2'>
                {isMobile ? (
                  <Button.Ripple className='gradient-light-primary text-white'>
                    <a className='text-white' href="tel:0979878582"  target='_blank'>Gọi điện</a>
                  </Button.Ripple>
                ) : (
                  ''
                )}
              </div>
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col className='d-flex justify-content-end flex-wrap mt-2' sm='12'>
          <Button.Ripple
            type='button'
            color='secondary'
            onClick={onClickBackHome}
          >
            <FormattedMessage id={`common.home`} />
          </Button.Ripple>
        </Col>
      </Row>
    </React.Fragment>
  )
}
export default ContactTab
