import { Field } from 'formik'
import React from 'react'
import { FormattedMessage } from 'react-intl'
import { FormGroup, Label } from 'reactstrap'
import { getPropObject } from '../configs/app-configs'

const BaseFormGroup = ({
  fieldName,
  errors,
  touched,
  messageId,
  type,
  disabled,
  isRequired = true
}) => {

  return (
    <FormGroup className='form-label-group position-relative'>
      <FormattedMessage id={messageId}>
        {(msg) => (
          <React.Fragment>
            <Field
              type={type}
              disabled={disabled}
              name={fieldName}
              className={`form-control ${
                isRequired &&  getPropObject(errors, fieldName) &&
                getPropObject(touched, fieldName)  && 'is-invalid'
              }`}
              placeholder={msg}
            />
            {isRequired && getPropObject(errors, fieldName) &&
            getPropObject(touched, fieldName) ? (
              <div className='text-danger'>{getPropObject(errors, fieldName)}</div>
            ) : null}
            <Label>{msg}</Label>
          </React.Fragment>
        )}
      </FormattedMessage>
    </FormGroup>
  )
}

export default BaseFormGroup
